/*   */

:root {
    --basic: black;
    --basic-alt: rgb(90,90,90);
    --bkg: white;
    --accent: rgb(92,128,188);
    --accent-highlighted: rgb(86,205,234);
}

html {
    height: 100%;
    cursor: url("./images/cursor_norm.png"),auto; /* Update graphic for better aim. */
    background-color: var(--bkg);
}

h1 {
    font-family: 'IBM Plex Mono', monospace;
    font-size: 36px;
    font-stretch: expanded;
    color: var(--basic);
    line-height: 0px;
}
p {
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 24px;
    font-stretch: expanded;
    color: var(--basic-alt);
}
label{
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 24px;
    color: var(--accent);
}

body {
    -webkit-user-select: none;  /* Chrome all / Safari all */
    -moz-user-select: none;     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */
    margin: 12px;
    background-color: transparent;
}

button {
    display: flex;
    align-items: center;
    border-radius: 10px;
    min-height:36px;
    min-width:36px;
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 24px;
    color: var(--basic);
    background-color: transparent;
    border: none;
}
    button:hover {
        -webkit-transition: all 0.5s ease;
        transition: all 0.5s ease;
        color: var(--bkg);
        cursor: url("./images/cursor_hover.png"),auto;
        background-color: gray;
    }

/* # */

#settings{
    position: absolute;
    z-index: 10;
    top: 12vh;
    right: 0;
    min-height:36px;
    width: 100px;
    text-align: center;
}

#iconbar {
    position: absolute;
    bottom: 10%;
    right: 0;
    background-color: transparent;
}

#resume-holder {
    width: 0;
    height: 90vh;
    transition: width 1.5s ease-in;
    background-color: var(--basic);
}

/* . */

.container {
    display: flex;
    flex-direction: column;
}
.item {
    max-width: 75%;
}

.blue-text{
    color: var(--accent); /* TODO: This is not working to override p colour. */
}

.dropdown-button {
    border-radius: 10px;
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 24px;
    color: var(--basic);
    background-color: transparent;
    border: none;
    height:inherit;
    padding: 8px;
}
    .dropdown-button:hover {
        -webkit-transition: all 0.5s ease;
        transition: all 0.5s ease;
        cursor: url("./images/cursor_hover.png"),auto;
    }

.logo {
    text-decoration: none;
    cursor: url("./images/cursor_norm.png"),auto;
    transition: 0.5s;
    position: absolute;
    right: 0px;
    max-width: 30px;
    overflow: hidden;
}
    .logo:hover {
        -webkit-transition: all 1s ease;
        transition: all 1s ease;
        opacity: 1;
        text-decoration: none;
        cursor: url("./images/cursor_hover.png"),auto;
        position: absolute;
        max-width: 48px;
    }

.router-link {
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 18px;
    color: var(--basic);
    text-decoration: none;
    font-weight: bold;
    justify-content: center;
    align-items: center;
    display: inline-flex;
    width: 100px;
    height: 9.9vh;
    transition: all .5s ease;
    padding: 5vh;
}
    .router-link:hover {
        color: var(--bkg);
        background-color: grey;
        cursor: url("./images/cursor_hover.png"),auto;
    }

.side-tab {
    border-radius: 0 50px 50px 0;
    background-color: var(--basic);
    color: var(--bkg);
    margin-top: 1vh;
    width: 120px;
    height: 120px;
}
    .side-tab:hover {
        color: var(--basic);
        background-color: var(--bkg);
        border: 1px var(--basic) solid;
    }

/* :: */

::-webkit-scrollbar {
    height: 5px;
    width: 5px;
}

::-webkit-scrollbar-track {
    background: var(--accent);
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #E8C54C; /* TODO: Update colour palette. */
    border-radius: 10px;
}

    ::-webkit-scrollbar-thumb:hover {
        background:#ffe897; /* TODO: Update colour palette. */
        border-radius: 10px;
    }

::selection {
    color: var(--basic);
    background: var(--accent-highlighted);
    }